export default{
	data(){
		return{
			oid:'',
			dataList:''
		}
	},
	onLoad(option) {
		console.log(option.oid)
		this.oid=option.oid;
		
		uni.loadFontFace ({
		  family: 'Alibaba PuHuiTi',
		  source: 'url("https://www.mastervan.cn/static/project/dolphin/static/font/alifont/AlibabaPuHuiTi-Light.ttf")',
		  success: function(){
				console.log('load font success')
			  },
			fail() {
				//console.log(456)
			}
		})
		let openId=uni.getStorageSync('openid');
		let cid = uni.getStorageSync('cid')
		let _this=this;
		uni.request({
			url:_this.$store.state.baseurl+'/order/findOrderMessageByOid',
			data:{
				openId:openId,
				oid:_this.oid,
				cid: cid
			},
			method:'POST',
			header : {'content-type':'application/x-www-form-urlencoded'},
			success:function(res){
				if(res.data.data.orderMessage!=undefined){
					var OrderMsgs = res.data.data.orderMessage
					var userType = 'c'+cid
					var typeList = OrderMsgs.ctypeStr.split("_")
					typeList.forEach(function(item) {
						if(item.slice(0,2) == userType) {
							var UserPrice = item.slice(3,item.length+1)
							OrderMsgs.ctypeStr = UserPrice
						}
					})
					_this.dataList=OrderMsgs
				}
			}
		})
	},
	computed: {
		isModel:function(){
			//console.log(this.$store.state.model)
			return this.$store.state.model;
		},
		isHeight:function(){
			return this.$store.state.windowHeight
		}
	},
	methods: {
		goback:function(){
			uni.navigateBack({
				delta:1
			})
		}
	}
}
